<template>
  <div v-loading="loading">
    <cms-breadcrumb to="/brand/list" @click="rest" type="list">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt collapse="true">
        <li>带 * 为必填项</li>
      </cms-prompt>
      <el-form
        ref="form"
        :rules="saveRules"
        :model="formData"
        label-width="210px"
        @submit.native.prevent
      >
        <el-form-item label="品牌名称" prop="name" :error="errorMessage.name">
          <el-input v-model="formData.name" maxlength="100"></el-input>
        </el-form-item>
        <el-form-item label="英文名称" prop="enName" :error="errorMessage.enName">
          <el-input v-model="formData.enName" maxlength="100"></el-input>
        </el-form-item>
        <el-form-item label="名称首字母" prop="initials" :error="errorMessage.initials">
          <el-input disabled v-model="formData.initials" maxlength="50"></el-input>
        </el-form-item>
        <el-form-item label="品牌LOGO" prop="logo" :error="errorMessage.logo" class="brands-logo">
          <cms-picture-zone v-model="formData.logo" v-if="status"></cms-picture-zone>
        </el-form-item>
        <el-form-item label="是否显示" prop="show" :error="errorMessage.show">
          <cms-switch v-model="formData.show"></cms-switch>
          <div class="gray">设置品牌是否推荐至品牌橱窗专题列表页醒目显示</div>
        </el-form-item>
        <el-form-item label="排序" prop="sortNum" :error="errorMessage.sortNum">
          <el-input v-model="formData.sortNum"  maxlength="2" ></el-input>
        </el-form-item>
        <el-form-item label="网址" prop="url" :error="errorMessage.url">
          <el-input v-model="formData.url" maxlength="100"></el-input>
          <div class="gray">填写带访问协议的访问域名</div>
        </el-form-item>
        <el-form-item label="品牌简介" prop="profile">
          <el-input v-model="formData.profile" type="textarea" :rows="6" maxlength="400"></el-input>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
        <cms-button @click="save(false)" v-prem="'/brand/save'" v-color>保存并新增</cms-button>
        <cms-button @click="save(true)" v-prem="'/brand/save'" v-color>保存</cms-button>
        <cms-button type="rest" @click="rest">重置</cms-button>
      </div>
    </div>
  </div>
</template>
<script>
import pinyin from 'js-pinyin'
import rules from '@/rules'
import form from '@/mixins/form.js'
import { brandSave } from '@/api/product/brand.js'

var defaultData = {
  name: '',
  enName: '',
  initials: '',
  logo: '',
  url: '',
  remark: '',
  profile: '',
  sortNum: '10',
  show: true
}
export default {
  mixins: [form],
  data() {
    return {
      status: true,
      formData: Object.assign({}, defaultData),
      saveRules: {
        // 新增规则
        name: [rules.required('请填写品牌名称'), rules.len(1, 150)],
        initials: [
          rules.required('请输入名称首字母', 'change'),
          rules.len(1, 50),
          rules.capital(),
          rules.english()
        ],
        logo: [rules.required('请上传品牌LOGO'), rules.number()],
        url: [rules.type('url')],
        show: [rules.required()],
        sortNum: [rules.required('请填写排序值'), rules.number()]
      }
    }
  },
  watch: {
    // 自动填充首字母
    formData: {
      handler(newV, oldV) {
        pinyin.setOptions({ checkPolyphone: false, charCase: 0 })
        if (newV.name === '') {
          return false
        } else {
          const arr = pinyin.getFullChars(newV.name)
          newV.initials = arr[0].toUpperCase()
        }
      },
      deep: true
    }
  },
  methods: {
    getLogo(items) {
      this.formData.logo = items[0].id
    },
    save(state) {
      // 添加
      this.saveForm(brandSave, this.formData, state, '/brand/list', 'list')
    }
  },
  created() {
    this.formData = Object.assign({}, defaultData)
    this.status = false
    setTimeout(() => {
      this.status = true
    }, 1)
  }
}
</script>



